<template>
  <div>
    <h1 class="text-center display-5 fw-bolder">Todo List!</h1>
  </div>
  
  
  <div class="input-group mb-3">
  <input @keyup.enter="addList();" v-model="text" type="text" class="form-control" placeholder="당신이 오늘 할일은 무엇인가요?" aria-label="Recipient's username" aria-describedby="button-addon2">
  <button 
  type="button" 
  class="btn btn-outline-success"
  @click = "addList();"
  >추가</button>

  </div>
 
 
  <ul v-for="(data, i) in datas" :key="i" class="list-group">
    <li class="list-group-item">
      <input
        @click="deleteList(i)"
        class="form-check-input me-1"
        type="checkbox"
        :value="data"
        :id="'checkbox' + i"
        v-model="checkedItems"
      />
      <label
        class="form-check-label stretched-link"
        :for="'checkbox' + i"
      >
        {{ data }}
      </label>
    </li>
  </ul>
</template>

<script>

export default {
  name: 'App',
  data(){
    return{
      datas : ["c","java","python",'javascript'],
      checkedItems: [], // 체크된 항목을 저장할 배열
      text : " "
  
    }
  },
  methods:{
    deleteList(i){
      console.log(i)
      if (i >= 0 && i < this.datas.length) {
        this.datas.splice(i, i+1);
      }
  },
  addList(){
    this.datas.push(this.text)
    this.text = ""
  }
}
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
</style>
